import React from 'react';
import './editAlbum.less';
import FileInput from "../fileInput/FileInput";
import {CrossIcon, SaveIcon} from "../Icons";
import Card from "./Card";
import http from '../../HttpService';

class EditAlbum extends React.Component {

    constructor(props) {
        super(props);
        this.pictures = [{
            id: 1,
            src: '/foto/download.jpg',
            order: 1
        }, {
            id: 2,
            src: '/foto/images.jpg',
            order: 2
        }];
        this.state = {
            dragStarted: false,
            dragFrom: null,
            dragFromEl: null,
            dragObj: null,
            isTextOpen: false
        }
    }

    componentDidMount() {
        http.doGet(routes.routes.GET_ALBUMS)
            .then(result => dispatch(setAlbums(result)));
    }

    openDetails() {
        this.setState({isTextOpen: true});
    }

    onCrossClick() {
        this.setState({isTextOpen: false});
    }

    deleteItem() {

    }

    changeDragState(obj) {
        this.setState(obj);
    }

    render() {
        const {isTextOpen} = this.state;
        return (
            <div className='edit_container'>
                {this.pictures.sort((x,y) => x.order-y.order).map(p => <Card
                    key={p.id}
                    picture={p}
                    pictures={this.pictures}
                    openDetails={() => this.openDetails()}
                    deleteItem={() => this.deleteItem()}
                    replaceImage={(target) => this.replaceImage(target)}
                    isTextOpen={isTextOpen}
                    changeDragState={(obj) => this.changeDragState(obj)}
                    dragState={this.state}
                />)}
                <FileInput className='new_image' disabled={isTextOpen}/>
                {isTextOpen &&
                <div className='new_text' ref={t => this.text = t}>
                    <input/>
                    <textarea/>
                    <SaveIcon className='save_icon'/>
                    <CrossIcon className='cross_icon' onClick={() => this.onCrossClick()}/>
                </div>}

            </div>
        );
    }
}

export default EditAlbum;



